<template>
    <section class="page">
       <div v-if="loadding">
            <el-button @click="test">测试</el-button>
       </div>
        <loadding v-else/>
    </section>
</template>

<script>
  import loadding from './../../components/loadding/loadding.vue'
export default {
  data () {
      return{
          loadding:true, //为true显示加载动画 为false显示页面
      }
  },
  components: {
      loadding
  },
  methods: {
      test(){
          this.$store.dispatch("testData",'1111'); //action 只能传递一个参数
      }
  }
}
</script>

<!--使用sass语法进行编译样式-->
<style lang="scss" scoped>
  @import "./../../assets/css/style";
  .mainContainer{
    line-height:0;
    background-color: $color1;

  }
</style>



